.row {
  margin-top: 50px;
} 
.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.bottom{
  text-align: center;
  position: absolute; 
  margin-top: 25px;
  margin-right: auto;
  margin-left: auto;
  left: 50%;
  transform: translate(-50%, 0);
}

.temperature {
  font-size: 42px;
}

.deg {
  vertical-align: 20px;
}